import { FC, useState } from 'react'
import { AutoComplete, Avatar, Badge, Button, Card, Checkbox, Divider, Form, Input, Radio, Select, Slider, Space, Upload } from 'antd'
import { RcFile } from 'antd/es/upload'
import { UploadOutlined } from '@ant-design/icons'

import { Config, avoids, coupling, expansions, jobs, orientations, progress, servers } from '../common'

interface OptionsProps {
  config: Config
  setConfig: (config: Config) => void
}

const Options: FC<OptionsProps> = ({ config, setConfig }) => {
  const [key, setKey] = useState('basic')

  const components = {
    basic: (
      <Space direction="vertical">
        <Form.Item label=" ">
          <Upload
            accept="image/*"
            listType="picture"
            maxCount={1}
            defaultFileList={config.image ? [{ uid: '1', name: 'image.png', url: config.image, status: 'done' }] : undefined}
            beforeUpload={() => false}
            onRemove={() => setConfig({ ...config, image: null })}
            onChange={(info) => {
              if (info.fileList.length) {
                const reader = new FileReader()
                reader.addEventListener('load', () => setConfig({ ...config, image: reader.result }))
                reader.readAsDataURL(info.file as RcFile)
              }
            }}
          >
            <Button icon={<UploadOutlined />}> </Button>
          </Upload>
        </Form.Item>

        <Form.Item label="  /">
          <Slider min={0} max={2} step={0.01} tooltip={{ formatter: (value) => `${Math.floor((value ?? 0) * 100)}%` }} defaultValue={config.scale} onChange={(scale) => setConfig({ ...config, scale })} />
        </Form.Item>

        <Form.Item label=" ">
          <Space size="small" wrap>
            {jobs.map((job, index) => {
              if (!job) return <Divider key={index} type="vertical" />
              const image = <Avatar key={job} size="small" shape="square" alt={job} src={`./icons/${job}.png`} onClick={() => setConfig({ ...config, job })} />

              return job === config.job ? (
                <Badge dot key={job}>
                  {image}
                </Badge>
              ) : (
                image
              )
            })}
          </Space>
        </Form.Item>

        <Form.Item label=" ">
          <Space.Compact style={{ width: '100%' }}>
            <Input value={config.name} onChange={(e) => setConfig({ ...config, name: e.target.value })} placeholder="" />
            <AutoComplete
              value={config.server}
              onChange={(server) => setConfig({ ...config, server })}
              placeholder=""
              options={servers.map((server) => ({
                ...server,
                options: server.options.map((o) => ({ label: o, value: o })),
              }))}
              filterOption={(inputValue, option) => option?.label.toLowerCase().indexOf(inputValue.toLowerCase()) !== -1}
            />
            <Input value={config.title} onChange={(e) => setConfig({ ...config, title: e.target.value })} placeholder="" />
          </Space.Compact>
        </Form.Item>
      </Space>
    ),

    theme: (
      <Space direction="vertical">
        <Form.Item label=" ">
          <Radio.Group
            value={config.color}
            onChange={(color) =>
              setConfig({
                ...config,
                color: color.target.value as Config['color'],
              })
            }
            defaultValue="black"
          >
            <Radio value="black"></Radio>
            <Radio value="white"></Radio>
          </Radio.Group>
        </Form.Item>
        <Form.Item label=" ">
          <Select
            value={config.font}
            onChange={(font) => setConfig({ ...config, font })}
            placeholder="  ."
            options={['', 'Noto Sans KR'].map((font) => ({
              value: font,
              label: <span style={{ fontFamily: font }}>{font}</span>,
            }))}
          />
        </Form.Item>
      </Space>
    ),

    ffxiv: (
      <Space direction="vertical">
        <Form.Item label=" ">
          <Space size="small" wrap>
            {jobs.map((job, index) => {
              if (!job) return <Divider key={index} type="vertical" />
              const image = (
                <Avatar
                  key={job}
                  size="small"
                  shape="square"
                  alt={job}
                  src={`./icons/${job}.png`}
                  onClick={() =>
                    setConfig({
                      ...config,
                      jobs: config.jobs.includes(job) ? config.jobs.filter((j) => j !== job) : [...config.jobs, job],
                    })
                  }
                />
              )

              return config.jobs.includes(job) ? (
                <Badge dot key={job}>
                  {image}
                </Badge>
              ) : (
                image
              )
            })}
          </Space>
        </Form.Item>

        <Form.Item label="  ">
          <Space wrap>
            <Select style={{ width: 225 }} value={config.progress} onChange={(progress) => setConfig({ ...config, progress })} placeholder=") v6.3 " options={progress} />
            <Select style={{ width: 150 }} value={config.expansion} onChange={(expansion) => setConfig({ ...config, expansion })} placeholder="" options={expansions} />
          </Space>
        </Form.Item>
        <Form.Item label=" ">
          <Space wrap>
            <Input style={{ width: 300 }} value={config.like} onChange={(e) => setConfig({ ...config, like: e.target.value })} placeholder="" />
            <Input style={{ width: 300 }} value={config.dislike} onChange={(e) => setConfig({ ...config, dislike: e.target.value })} placeholder="" />
          </Space>
        </Form.Item>
      </Space>
    ),

    mastodon: (
      <Space direction="vertical">
        <Form.Item label="">
          <Space wrap>
            <Input style={{ width: 300 }} value={config.mastodonName} onChange={(e) => setConfig({ ...config, mastodonName: e.target.value })} placeholder=" " />
            <Input style={{ width: 300 }} value={config.handle} onChange={(e) => setConfig({ ...config, handle: e.target.value })} placeholder=" (: @chalk@chalk.moe)" />
          </Space>
        </Form.Item>
        <Form.Item label="">
          <Space wrap>
            <Input style={{ width: 300 }} value={config.mastodonMain} onChange={(e) => setConfig({ ...config, mastodonMain: e.target.value })} placeholder="  " />
            <Input style={{ width: 300 }} value={config.mastodonSub} onChange={(e) => setConfig({ ...config, mastodonSub: e.target.value })} placeholder="  " />
          </Space>
        </Form.Item>
        <Form.Item label=" ">
          <Checkbox.Group options={orientations} value={config.orientations} onChange={(values) => setConfig({ ...config, orientations: values.map((x) => x.toString()) })} />
        </Form.Item>
        <Form.Item label=" ">
          <Checkbox.Group options={coupling} value={config.couplings} onChange={(values) => setConfig({ ...config, couplings: values.map((x) => x.toString()) })} />
        </Form.Item>
        <Form.Item label=" ">
          <Checkbox.Group options={avoids} value={config.avoids} onChange={(values) => setConfig({ ...config, avoids: values.map((x) => x.toString()) })} />
        </Form.Item>
      </Space>
    ),
  }

  const tabs: { key: keyof typeof components; tab: string }[] = [
    { key: 'basic', tab: '' },
    { key: 'theme', tab: '' },
    { key: 'ffxiv', tab: '14' },
    { key: 'mastodon', tab: '' },
  ]

  return (
    <Card style={{ marginTop: 10 }} tabList={tabs} activeTabKey={key} onTabChange={setKey}>
      <Form layout="vertical">{components[key as keyof typeof components]}</Form>
    </Card>
  )
}

export default Options
